import React, { useState, useRef, useEffect } from 'react'
import { Form, Formik } from 'formik'
import * as Yup from 'yup'
import axios from 'axios'
import { useLocation } from '@reach/router'
import { format } from 'date-fns'
import Layout from './../layout'
import {
  SEO,
  StripeBar,
  Banner,
  FormInput as Input,
  FormTextarea as Textarea,
  Button,
  FormDate,
  FormMultiSelect,
  Recaptcha,
} from './../components'
import { encodeForm } from './../utils'

import {
  Content,
  Desc,
  Message,
  Asterisk,
} from './../containers/ShareEvent/style'

interface IPulsePageProps {
  location: {
    pathname: string
  }
  pageContext: {
    seoKeyword: string
    seoDescription: string
  }
}

const initValues = {
  title: '',
  eventStartDate: '',
  eventEndDate: '',
  eventCategory: [],
  venueAddress: '',
  venueCity: '',
  venueProvince: '',
  venuePostalCode: '',
  venuePhone: '',
  venueWebsite: '',
  organizerName: '',
  organizerPhone: '',
  organizerWebsite: '',
  organizerEmail: '',
  eventUrl: '',
  eventCost: '',
  eventDescription: '',
}
export default ({
  location,
  pageContext: { seoKeyword, seoDescription },
}: IPulsePageProps) => {
  const [error, setError] = useState('')
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')
  const recaptchaRef = useRef()

  const validationSchema = Yup.object().shape({
    title: Yup.string().required(
      isFr
        ? 'Le titre de l’événement doit être indiqué'
        : 'Event Title is required'
    ),
    eventStartDate: Yup.string().required(
      isFr ? 'La date de début est obligatoire' : 'Event Start Date is required'
    ),
    eventEndDate: Yup.string().required(
      isFr ? 'La date de fin est nécessaire' : 'Event End Date is required'
    ),
    eventCategory: Yup.array().required(
      isFr ? 'La catégorie est obligatoire' : 'Event Category is required'
    ),
    eventDescription: Yup.string().required(
      isFr
        ? 'La description est un champ obligatoire.'
        : ' Description is required'
    ),
    eventCost: Yup.number().integer(
      isFr
        ? 'Coût doit être un chiffre valide.'
        : 'Cost must be a valid number.'
    ),
    venuePostalCode: Yup.string().matches(
      /^(?!.*[DFIOQU])[A-VXY][0-9][A-Z] ?[0-9][A-Z][0-9]$/,
      'Should be a valid postal code.'
    ),
    organizerWebsite: Yup.string().matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      'Should be a valid url.'
    ),
    venueWebsite: Yup.string().matches(
      /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
      'Should be a valid url.'
    ),
    organizerEmail: Yup.string().email('Should be valid email.'),
    organizerPhone: Yup.string().matches(
      /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
      'Phone number should be 123-456-7890 format.'
    ),
    venuePhone: Yup.string().matches(
      /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/,
      'Phone number should be 123-456-7890 format.'
    ),
    recaptcha: Yup.boolean().required(
      isFr
        ? "Veuillez vérifier que vous n'êtes pas un robot."
        : 'Please verify you are not a robot.'
    ),
  })
  useEffect(() => {
    delete window.recaptchaOptions
    delete window.grecaptcha
    window.recaptchaOptions = {
      lang: 'fr',
      useRecaptchaNet: false,
      removeOnUnmount: false,
    }
    return () => {}
  }, [])
  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        title={isFr ? 'Partagez un événement' : 'Share an Event'}
        keyword={seoKeyword}
        description={seoDescription}
      />
      <Banner colors={['#397897', '#00587F', '#80D0DB']}>
        <h1>{isFr ? 'Partagez un événement' : 'Share an Event'}</h1>
        <h2 className='mt-4'>
          {isFr
            ? 'Envoyez-nous le lien d’un événement à venir que vous aimeriez que nous ajoutions et nous le publierons ici!'
            : 'Submit an upcoming event you’d like us to include and we’ll post it on our events page.'}
        </h2>
      </Banner>
      <StripeBar colors={['#E58630', '#DA5027', '#D1932A', '#D16428']} />
      <Content>
        <div className='container py-4'>
          <Formik
            initialValues={initValues}
            validationSchema={validationSchema}
            onSubmit={async (values, { resetForm }) => {
              // await executeRecaptcha('6LdmMj0aAAAAAIvn1bIItA3cbg90sgOCadNjbRTU')
              console.log('values', values)
              values['titleFr'] = values['title']
              values['eventDescriptionFr'] = values['eventDescription']
              let temp = ['---']
              temp = [
                ...temp,
                ...Object.keys(values).map(key => {
                  if (typeof values[key] === 'string') {
                    return `${key}: "${values[key]
                      .split('\n')
                      .join('     \r\n\r\n  ')}"`
                  } else if (Array.isArray(values[key])) {
                    return `${key}: ${values[key]
                      .map((d: string) => `     \r\n  - "${d}"`)
                      .join('')}`
                  } else if (typeof values[key] === 'number') {
                    return `${key}: "${values[key]}"`
                  } else {
                    return `${key}: ${values[key]}`
                  }
                }),
              ]
              temp.push('---')
              axios
                .post(
                  `https://gitlab.com/api/v4/projects/22180390/repository/files/content%2Fevents%2F${format(
                    new Date(),
                    'MM-dd-yyyy-HH-mm-ss'
                  )}%2Findex.md`,
                  {
                    branch: 'master',
                    author_email: 'support@deplume.ca',
                    author_name: 'User_Submission',
                    content: temp.join('     \r\n'),
                    commit_message: `Event ${values.title} is created.`,
                  },
                  {
                    headers: {
                      'PRIVATE-TOKEN': 'CZiBaqQd5NGDyzLEpPxw',
                    },
                  }
                )
                .then(res => {
                  fetch('https://generation4equality.ca/shareEvent', {
                    method: 'POST',
                    headers: {
                      'Content-Type': 'application/x-www-form-urlencoded',
                    },
                    body: encodeForm({
                      'form-name': 'event',
                      ...values,
                    }),
                  })
                    .then(() => {
                      setError(
                        isFr
                          ? 'Merci! Nous avons bien reçu votre soumission.'
                          : 'Thank you for submitting successfully!'
                      )
                      resetForm()
                    })
                    .catch(error => {
                      console.log('error', error)
                      setError(isFr ? "Désolé, une erreur s\'est produite. Veuillez réessayer ultérieurement." : "Sorry, an unexpected error occurred. Please try again.")
                    })
                })
                .catch(err => {
                  console.log('error', err)
                  setError(isFr ? "Désolé, une erreur s\'est produite. Veuillez réessayer ultérieurement." : "Sorry, an unexpected error occurred. Please try again.")
                })
            }}
          >
            {({ setValues, values }) => (
              <Form
                data-netlify={true}
                name='event'
                // data-netlify-recaptcha='true'
              >
                <h2>{isFr ? 'Soumettez un événement' : 'Submit An Event'}</h2>
                <Desc className='my-4'>
                  {isFr ? (
                    <>
                      Les champs marqués d'un <Asterisk>*</Asterisk> sont
                      obligatoires
                    </>
                  ) : (
                    <>
                      Fields marked with an <Asterisk>*</Asterisk> are required
                    </>
                  )}
                </Desc>
                <div className='d-flex flex-wrap row'>
                  <div className='col-md-6'>
                    <h2>{isFr ? "Détails de l'événement" : 'Event Details'}</h2>
                    <Input
                      label='Event Title'
                      labelFr="Titre de l'événement"
                      name='title'
                      className='mt-4 mb-3'
                      required
                    />
                    <FormDate
                      label={isFr ? 'Date de début' : 'Start Date'}
                      name='eventStartDate'
                      className='mb-3'
                      required
                    />
                    <FormDate
                      label={isFr ? 'Date de fin' : 'End Date'}
                      name='eventEndDate'
                      className='mb-3'
                      required
                    />
                    <FormMultiSelect
                      label={
                        isFr ? 'CATÉGORIES D’ÉVÉNEMENTS' : 'Event Category'
                      }
                      name='eventCategory'
                      required
                      data={[
                        { label: 'Youth', labelFr: 'Jeunes', value: 'Youth' },
                        {
                          label: 'Indigenous',
                          labelFr: 'Autochtones',
                          value: 'Indigenous',
                        },
                        {
                          label: '2SLGBTQQIA',
                          labelFr: '2SLGBTQQIA',
                          value: '2SLGBTQQIA',
                        },
                      ]}
                    />
                    <Input
                      label='Website'
                      labelFr='Site Web'
                      name='eventUrl'
                      className='mb-3'
                    />
                    <Input
                      label='Cost'
                      labelFr='Coût'
                      type='number'
                      name='eventCost'
                      className='mb-3'
                    />
                    <Textarea
                      name='eventDescription'
                      label={isFr ? 'La description' : 'Description'}
                      className='mb-3'
                      required
                    />
                  </div>
                  <div className='col-md-6'>
                    <h2>{isFr ? 'ENDROIT' : 'Venue'}</h2>
                    <Input
                      label='Address'
                      labelFr='Adresse'
                      name='venueAddress'
                      className='mt-4 mb-3'
                    />
                    <Input
                      label='City'
                      labelFr='Ville'
                      name='venueCity'
                      className='mb-3'
                    />
                    <Input
                      label='Province'
                      labelFr='Province'
                      name='venueProvince'
                      className='mb-3'
                    />
                    <Input
                      label='Postal Code'
                      labelFr='Code postal'
                      name='venuePostalCode'
                      className='mb-3'
                    />
                    <Input
                      label='Phone'
                      labelFr='Téléphone'
                      name='venuePhone'
                      className='mb-3'
                    />
                    <Input
                      label='Website'
                      labelFr='Site Web'
                      name='venueWebsite'
                      className='mb-3'
                    />
                    <h2>{isFr ? 'Organisateur' : 'Organizer'}</h2>
                    <Input
                      label='Name'
                      labelFr='Nom'
                      name='organizerName'
                      className='mt-4 mb-3'
                    />
                    <Input
                      label='Phone'
                      labelFr='Téléphone'
                      name='organizerPhone'
                      className='mb-3'
                    />
                    <Input
                      label='Website'
                      labelFr='Site Web'
                      name='organizerWebsite'
                      className='mb-3'
                    />
                    <Input
                      label='Email'
                      labelFr='Courriel'
                      name='organizerEmail'
                      className='mb-3'
                    />
                  </div>
                </div>
                <Recaptcha name='recaptcha' />

                {error && <Message className='mb-4'>{error}</Message>}
                <div className='mt-4 d-flex justify-content-center'>
                  <Button buttonType='submit' background='light'>
                    {isFr ? 'Soumettre votre événement' : 'Submit Your Event'}
                  </Button>
                </div>
              </Form>
            )}
          </Formik>
        </div>
      </Content>
    </Layout>
  )
}
